import React, { useEffect, useState, useCallback, forwardRef, useImperativeHandle } from 'react';
import { Table, Spinner, Alert, Button, Form, Row, Col } from 'react-bootstrap';
import { booksApi, authorsApi, genresApi, ratingsApi } from '../../lib/supabase';
import EditBook from '../modals/Book/EditBook';
import ConfirmDialog from '../modals/Dialoge/ConfirmDialog';
import { CaretUpFill, CaretDownFill } from 'react-bootstrap-icons';
import CreateBook from '../modals/Book/CreateBook';
import { loaderStyles } from '../../styles/loaderStyles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEdit, faTrash, faPlus } from '@fortawesome/free-solid-svg-icons';

const BooksTable = forwardRef(({ searchQuery = '' }, ref) => {
    const [books, setBooks] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [editBook, setEditBook] = useState(null);
    const [showEditModal, setShowEditModal] = useState(false);
    const [showDeleteDialog, setShowDeleteDialog] = useState(false);
    const [bookToDelete, setBookToDelete] = useState(null);
    const [showCreateModal, setShowCreateModal] = useState(false);
    const [selectedAuthor, setSelectedAuthor] = useState('all');
    const [selectedGenre, setSelectedGenre] = useState('all');
    const [selectedPublisher, setSelectedPublisher] = useState('all');
    const [selectedYear, setSelectedYear] = useState('all');
    const [authors, setAuthors] = useState([]);
    const [genres, setGenres] = useState([]);
    const [publishers, setPublishers] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(8);
    const [sortField, setSortField] = useState('title');
    const [sortDirection, setSortDirection] = useState('asc');
    const [filters, setFilters] = useState({
        author: 'all',
        genre: 'all',
        year: 'all'
    });
    const [years, setYears] = useState([]);

    const handleSort = (field) => {
        if (sortField === field) {
            setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
        } else {
            setSortField(field);
            setSortDirection('asc');
        }
    };

    const loadBooks = useCallback(async () => {
        try {
            setLoading(true);
            setError(null);

            const { data, error } = await booksApi.getBooks({
                searchQuery,
                filters,
                sortField,
                sortDirection
            });

            if (error) throw error;
            setBooks(data || []);
            setError(null);
        } catch (error) {
            console.error('   :', error);
            setError('    ');
        } finally {
            setLoading(false);
        }
    }, [searchQuery, filters, sortField, sortDirection]);

    useImperativeHandle(ref, () => ({
        loadBooks
    }));

    useEffect(() => {
        loadBooks();
        loadFiltersData();

        const handleUpdate = () => {
            loadBooks();
        };

        window.addEventListener('updateBooksTable', handleUpdate);
        return () => {
            window.removeEventListener('updateBooksTable', handleUpdate);
        };
    }, [loadBooks]);

    const loadFiltersData = async () => {
        try {
            //  
            const { data: authorsData, error: authorsError } = await authorsApi.getAuthors();
            if (authorsError) throw authorsError;
            setAuthors(authorsData || []);

            //  
            const { data: genresData, error: genresError } = await genresApi.getGenres();
            if (genresError) throw genresError;
            setGenres(genresData || []);

            //     
            const { data: yearsData, error: yearsError } = await booksApi.getPublicationYears();
            if (yearsError) throw yearsError;
            
            const uniqueYears = [...new Set(yearsData.map(book => book.publication_year))];
            setYears(uniqueYears);
        } catch (error) {
            console.error('     :', error);
            setError('     ');
        }
    };

    const handleEdit = (book) => {
        setEditBook(book);
        setShowEditModal(true);
    };

    const handleDelete = async (book) => {
        setBookToDelete(book);
        setShowDeleteDialog(true);
    };

    const confirmDelete = async () => {
        try {
            //    
            const { data: ratings, error: ratingsError } = await ratingsApi.getBookRatings(bookToDelete.id);
            if (ratingsError) throw ratingsError;

            if (ratings && ratings.length > 0) {
                //   
                await ratingsApi.deleteBookRatings(bookToDelete.id);
            }

            //  
            const { error: deleteError } = await booksApi.deleteBook(bookToDelete.id);
            if (deleteError) throw deleteError;
            
            await loadBooks();
            window.dispatchEvent(new Event('updateRatingsTable'));
            setShowDeleteDialog(false);
            setBookToDelete(null);
        } catch (e) {
            console.error('   :', e);
            setError('   ');
        }
    };

    const getFilteredBooks = () => {
        return books.filter(book => {
            const matchesAuthor = selectedAuthor === 'all' || book.author_id === selectedAuthor;
            const matchesGenre = selectedGenre === 'all' || book.genre_id === selectedGenre;
            const matchesPublisher = selectedPublisher === 'all' || book.publisher_id === selectedPublisher;
            const matchesYear = selectedYear === 'all' || book.publication_year === selectedYear;
            return matchesAuthor && matchesGenre && matchesPublisher && matchesYear;
        });
    };

    if (loading && !books.length) {
        return (
            <div style={loaderStyles.container}>
                <Spinner animation="border" style={loaderStyles.spinner} />
            </div>
        );
    }

    if (error) {
        return <Alert variant="danger">{error}</Alert>;
    }

    const sortedAndFilteredBooks = getFilteredBooks().sort((a, b) => {
        let aValue, bValue;
        
        switch(sortField) {
            case 'title':
                aValue = a.title || '';
                bValue = b.title || '';
                break;
            case 'author':
                aValue = a.author?.name || '';
                bValue = b.author?.name || '';
                break;
            case 'genre':
                aValue = a.genre?.name || '';
                bValue = b.genre?.name || '';
                break;
            case 'publisher':
                aValue = a.publisher?.name || '';
                bValue = b.publisher?.name || '';
                break;
            case 'year':
                aValue = Number(a.publication_year);
                bValue = Number(b.publication_year);
                break;
            case 'quantity':
                aValue = Number(a.available_copies);
                bValue = Number(b.available_copies);
                break;
            default:
                aValue = '';
                bValue = '';
        }

        if (aValue < bValue) return sortDirection === 'asc' ? -1 : 1;
        if (aValue > bValue) return sortDirection === 'asc' ? 1 : -1;
        return 0;
    });
    
    // 
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = sortedAndFilteredBooks.slice(indexOfFirstItem, indexOfLastItem);
    const totalPages = Math.max(1, Math.ceil(sortedAndFilteredBooks.length / itemsPerPage));

    const pageItemStyle = {
        width: '32px',
        height: '32px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        border: '1px solid #dee2e6',
        borderRadius: '4px',
        cursor: 'pointer',
        backgroundColor: 'white',
        color: '#0d6efd',
        textDecoration: 'none',
        fontSize: '14px'
    };

    const activePageStyle = {
        ...pageItemStyle,
        backgroundColor: '#0d6efd',
        color: 'white',
        border: '1px solid #0d6efd'
    };

    const disabledPageStyle = {
        ...pageItemStyle,
        cursor: 'not-allowed',
        color: '#6c757d',
        backgroundColor: '#e9ecef'
    };

    const headerStyle = {
        cursor: 'pointer',
        userSelect: 'none',
        display: 'flex',
        alignItems: 'center',
        gap: '5px'
    };

    const SortIcon = ({ direction }) => {
        if (!direction) return null;
        return direction === 'asc' ? '' : '';
    };

    return (
        <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
            <div style={{ flex: '1', overflowY: 'auto', marginBottom: '20px', display: 'flex', flexDirection: 'column', minHeight: '510px' }}>
                <div className="mb-3 d-flex justify-content-between align-items-center">
                    <div className="d-flex gap-2" style={{ flex: 1 }}>
                        <Form.Group style={{ width: '150px' }}>
                            <Form.Select 
                                value={selectedAuthor}
                                onChange={(e) => setSelectedAuthor(e.target.value)}
                                size="sm"
                            >
                                <option value="all"> </option>
                                {authors.map(author => (
                                    <option key={author.id} value={author.id}>
                                        {author.name}
                                    </option>
                                ))}
                            </Form.Select>
                        </Form.Group>

                        <Form.Group style={{ width: '150px' }}>
                            <Form.Select 
                                value={selectedGenre}
                                onChange={(e) => setSelectedGenre(e.target.value)}
                                size="sm"
                            >
                                <option value="all"> </option>
                                {genres.map(genre => (
                                    <option key={genre.id} value={genre.id}>
                                        {genre.name}
                                    </option>
                                ))}
                            </Form.Select>
                        </Form.Group>

                        <Form.Group style={{ width: '150px' }}>
                            <Form.Select 
                                value={selectedYear}
                                onChange={(e) => setSelectedYear(e.target.value)}
                                size="sm"
                            >
                                <option value="all"> </option>
                                {years.map(year => (
                                    <option key={year} value={year}>
                                        {year}
                                    </option>
                                ))}
                            </Form.Select>
                        </Form.Group>
                    </div>

                    <Button 
                        variant="success" 
                        onClick={() => setShowCreateModal(true)}
                        style={{ transition: 'all 0.2s' }}
                        className="text-white ms-3"
                    >
                        <FontAwesomeIcon icon={faPlus} />  
                    </Button>
                </div>

                <Table striped bordered hover style={{ tableLayout: 'fixed' }}>
                    <thead>
                        <tr style={{ height: '45px' }}>
                            <th onClick={() => handleSort('title')} style={headerStyle}>
                                 <SortIcon direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('author')} style={headerStyle}>
                                 <SortIcon direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('genre')} style={headerStyle}>
                                 <SortIcon direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('year')} style={headerStyle}>
                                 <SortIcon direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('quantity')} style={headerStyle}>
                                 <SortIcon direction={sortDirection} />
                            </th>
                            <th style={{ width: '200px' }}></th>
                        </tr>
                    </thead>
                    <tbody>
                        {currentItems.map(book => (
                            <tr key={book.id} style={{ height: '45px' }}>
                                <td>{book.title}</td>
                                <td>{book.author?.name}</td>
                                <td>{book.genre?.name}</td>
                                <td>{book.publication_year}</td>
                                <td>{book.available_copies}</td>
                                <td>
                                    <div className="d-flex gap-1">
                                        <Button 
                                            variant="primary" 
                                            size="sm"
                                            onClick={() => handleEdit(book)}
                                            style={{ padding: '0.2rem 0.5rem', borderRadius: '0.2rem' }}
                                        >
                                            <FontAwesomeIcon icon={faEdit} />
                                        </Button>
                                        <Button 
                                            variant="danger" 
                                            size="sm"
                                            onClick={() => handleDelete(book)}
                                            style={{ padding: '0.2rem 0.5rem', borderRadius: '0.2rem' }}
                                        >
                                            <FontAwesomeIcon icon={faTrash} />
                                        </Button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                        {currentItems.length === 0 && (
                            <tr>
                                <td colSpan="7" className="text-center">
                                    {searchQuery ? '  ' : '  '}
                                </td>
                            </tr>
                        )}
                    </tbody>
                </Table>
            </div>

            <div style={{ position: 'sticky', bottom: 0, backgroundColor: 'white', paddingTop: '10px', borderTop: '1px solid #dee2e6' }} className="d-flex justify-content-center gap-2">
                <button 
                    style={currentPage === 1 ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(1)}
                    disabled={currentPage === 1}
                >
                    
                </button>
                <button 
                    style={currentPage === 1 ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                    disabled={currentPage === 1}
                >
                    
                </button>
                {[...Array(totalPages)].map((_, idx) => {
                    const pageNumber = idx + 1;
                    return (
                        <button
                            key={pageNumber}
                            style={pageNumber === currentPage ? activePageStyle : pageItemStyle}
                            onClick={() => setCurrentPage(pageNumber)}
                        >
                            {pageNumber}
                        </button>
                    );
                })}
                <button 
                    style={currentPage === totalPages ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                    disabled={currentPage === totalPages}
                >
                    
                </button>
                <button 
                    style={currentPage === totalPages ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(totalPages)}
                    disabled={currentPage === totalPages}
                >
                    
                </button>
            </div>

            <EditBook
                show={showEditModal}
                onHide={() => {
                    setShowEditModal(false);
                    setEditBook(null);
                }}
                book={editBook}
                onSuccess={loadBooks}
            />

            <ConfirmDialog
                show={showDeleteDialog}
                onHide={() => {
                    setShowDeleteDialog(false);
                    setBookToDelete(null);
                }}
                onConfirm={confirmDelete}
                title=" "
                message={`     "${bookToDelete?.title}"?`}
            />

            <CreateBook 
                show={showCreateModal} 
                onHide={() => {
                    setShowCreateModal(false);
                    setSelectedAuthor('all');
                    setSelectedGenre('all');
                    setSelectedYear('all');
                }}
                onSuccess={loadBooks}
            />
        </div>
    );
});

export default BooksTable; 