import React, { useEffect, useState, useCallback, forwardRef, useImperativeHandle } from 'react';
import { Table, Spinner, Alert, Button, Form } from 'react-bootstrap';
import { genresApi, booksApi } from '../../lib/supabase';
import EditGenre from '../modals/Genre/EditGenre';
import ConfirmDialog from '../modals/Dialoge/ConfirmDialog';
import CreateGenre from '../modals/Genre/CreateGenre';
import { loaderStyles } from '../../styles/loaderStyles';
import SortIcon from './SortIcon';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEdit, faTrash, faSort, faSortUp, faSortDown } from '@fortawesome/free-solid-svg-icons';
import './Table.css';
import { PencilFill, TrashFill } from 'react-bootstrap-icons';

const headerStyle = {
    cursor: 'pointer',
    userSelect: 'none',
    backgroundColor: '#f8f9fa',
    position: 'sticky',
    top: 0,
    zIndex: 1
};

const GenresTable = forwardRef(({ searchQuery = '' }, ref) => {
    const [genres, setGenres] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [editGenre, setEditGenre] = useState(null);
    const [showEditModal, setShowEditModal] = useState(false);
    const [showDeleteModal, setShowDeleteModal] = useState(false);
    const [genreToDelete, setGenreToDelete] = useState(null);
    const [showCreateModal, setShowCreateModal] = useState(false);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(8);
    const [sortField, setSortField] = useState('name');
    const [sortDirection, setSortDirection] = useState('asc');
    const [deleteError, setDeleteError] = useState('');
    const [selectedGenre, setSelectedGenre] = useState(null);
    const [showDeleteDialog, setShowDeleteDialog] = useState(false);

    const handleSort = (field) => {
        if (sortField === field) {
            setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
        } else {
            setSortField(field);
            setSortDirection('asc');
        }
    };

    const loadGenres = useCallback(async () => {
        try {
            setLoading(true);
            const { data, error } = await genresApi.getGenres({
                sortField,
                sortDirection
            });

            if (error) throw error;
            
            setGenres(data || []);
            setError(null);
        } catch (e) {
            console.error('   :', e);
            setError('   ');
        } finally {
            setLoading(false);
        }
    }, [sortField, sortDirection]);

    useImperativeHandle(ref, () => ({
        loadGenres
    }));

    useEffect(() => {
        loadGenres();

        const handleUpdate = () => {
            loadGenres();
        };

        window.addEventListener('updateGenresTable', handleUpdate);
        return () => {
            window.removeEventListener('updateGenresTable', handleUpdate);
        };
    }, [loadGenres]);

    const handleEdit = (genre) => {
        setEditGenre(genre);
        setShowEditModal(true);
    };

    const handleDelete = async (genre) => {
        setGenreToDelete(genre);
        setShowDeleteModal(true);
    };

    const confirmDelete = async () => {
        try {
            //    
            const { data: books, error: booksError } = await booksApi.getBooksByGenre(genreToDelete.id);
            if (booksError) throw booksError;

            if (books && books.length > 0) {
                setError('  ,    ');
                setShowDeleteModal(false);
                setGenreToDelete(null);
                return;
            }

            //  
            const { error: deleteError } = await genresApi.deleteGenre(genreToDelete.id);
            if (deleteError) throw deleteError;
            
            await loadGenres();
            setShowDeleteModal(false);
            setGenreToDelete(null);
        } catch (e) {
            console.error('   :', e);
            setError('   ');
        }
    };

    if (loading && !genres.length) {
        return (
            <div style={loaderStyles.container}>
                <Spinner animation="border" style={loaderStyles.spinner} />
            </div>
        );
    }

    if (error) {
        return <Alert variant="danger">{error}</Alert>;
    }

    const sortedGenres = [...genres].sort((a, b) => {
        let aValue, bValue;
        
        switch(sortField) {
            case 'name':
                aValue = a.name || '';
                bValue = b.name || '';
                break;
            case 'created_at':
                aValue = new Date(a.created_at);
                bValue = new Date(b.created_at);
                break;
            default:
                aValue = '';
                bValue = '';
        }

        if (aValue < bValue) return sortDirection === 'asc' ? -1 : 1;
        if (aValue > bValue) return sortDirection === 'asc' ? 1 : -1;
        return 0;
    });
    
    // 
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = sortedGenres.slice(indexOfFirstItem, indexOfLastItem);
    const totalPages = Math.max(1, Math.ceil(sortedGenres.length / itemsPerPage));

    const pageItemStyle = {
        width: '32px',
        height: '32px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        border: '1px solid #dee2e6',
        borderRadius: '4px',
        cursor: 'pointer',
        backgroundColor: 'white',
        color: '#0d6efd',
        textDecoration: 'none',
        fontSize: '14px'
    };

    const activePageStyle = {
        ...pageItemStyle,
        backgroundColor: '#0d6efd',
        color: 'white',
        border: '1px solid #0d6efd'
    };

    const disabledPageStyle = {
        ...pageItemStyle,
        cursor: 'not-allowed',
        color: '#6c757d',
        backgroundColor: '#e9ecef'
    };

    return (
        <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
            <div style={{ flex: '1', overflowY: 'auto', marginBottom: '20px', display: 'flex', flexDirection: 'column', minHeight: '510px' }}>
                <div className="mb-3 d-flex justify-content-end">
                    <Button 
                        variant="success" 
                        onClick={() => setShowCreateModal(true)}
                        style={{ transition: 'all 0.2s' }}
                        className="text-white"
                    >
                         
                    </Button>
                </div>

                <Table striped bordered hover style={{ tableLayout: 'fixed' }}>
                    <thead>
                        <tr style={{ height: '45px' }}>
                            <th onClick={() => handleSort('name')} style={headerStyle}>
                                 <SortIcon field="name" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('created_at')} style={headerStyle}>
                                  <SortIcon field="created_at" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th style={{ width: '200px' }}></th>
                        </tr>
                    </thead>
                    <tbody>
                        {currentItems.map(genre => (
                            <tr key={genre.id} style={{ height: '45px' }}>
                                <td>{genre.name}</td>
                                <td>{new Date(genre.created_at).toLocaleDateString()}</td>
                                <td>
                                    <div className="d-flex gap-1">
                                        <Button 
                                            variant="primary" 
                                            size="sm"
                                            onClick={() => handleEdit(genre)}
                                            style={{ padding: '0.2rem 0.5rem', borderRadius: '0.2rem' }}
                                        >
                                            
                                        </Button>
                                        <Button 
                                            variant="danger" 
                                            size="sm"
                                            onClick={() => handleDelete(genre)}
                                            style={{ padding: '0.2rem 0.5rem', borderRadius: '0.2rem' }}
                                        >
                                            
                                        </Button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                        {currentItems.length === 0 && (
                            <tr>
                                <td colSpan="3" className="text-center">
                                    {searchQuery ? '  ' : '  '}
                                </td>
                            </tr>
                        )}
                    </tbody>
                </Table>
            </div>

            <div style={{ position: 'sticky', bottom: 0, backgroundColor: 'white', paddingTop: '10px', borderTop: '1px solid #dee2e6' }} className="d-flex justify-content-center gap-2">
                <button 
                    style={currentPage === 1 ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(1)}
                    disabled={currentPage === 1}
                >
                    
                </button>
                <button 
                    style={currentPage === 1 ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                    disabled={currentPage === 1}
                >
                    
                </button>
                {[...Array(totalPages)].map((_, idx) => {
                    const pageNumber = idx + 1;
                    return (
                        <button
                            key={pageNumber}
                            style={pageNumber === currentPage ? activePageStyle : pageItemStyle}
                            onClick={() => setCurrentPage(pageNumber)}
                        >
                            {pageNumber}
                        </button>
                    );
                })}
                <button 
                    style={currentPage === totalPages ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                    disabled={currentPage === totalPages}
                >
                    
                </button>
                <button 
                    style={currentPage === totalPages ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(totalPages)}
                    disabled={currentPage === totalPages}
                >
                    
                </button>
            </div>

            <CreateGenre
                show={showCreateModal}
                onHide={() => setShowCreateModal(false)}
                onSuccess={loadGenres}
            />

            <EditGenre
                show={showEditModal}
                onHide={() => {
                    setShowEditModal(false);
                    setEditGenre(null);
                }}
                genre={editGenre}
                onSuccess={loadGenres}
            />

            <ConfirmDialog
                show={showDeleteDialog}
                onHide={() => {
                    setShowDeleteDialog(false);
                    setGenreToDelete(null);
                }}
                onConfirm={confirmDelete}
                title=" "
                message={`     "${genreToDelete?.name}"?`}
            />
        </div>
    );
});

export default GenresTable; 