import React from 'react';
import { Modal, Button, Alert } from 'react-bootstrap';
import './ConfirmDialog.css';

const ConfirmDialog = ({ show, onHide, onConfirm, title, message, canDelete = true }) => {
    return (
        <Modal show={show} onHide={onHide} centered className="confirm-dialog">
            <Modal.Header closeButton>
                <Modal.Title>{title}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {message}
                {!canDelete && (
                    <Alert 
                        variant="warning" 
                        className="confirm-alert"
                    >
                               
                    </Alert>
                )}
            </Modal.Body>
            <Modal.Footer>
                <Button 
                    variant="secondary" 
                    onClick={onHide}
                    className="confirm-button secondary"
                >
                    {canDelete ? '' : ''}
                </Button>
                {canDelete && (
                    <Button 
                        variant="danger" 
                        onClick={onConfirm}
                        className="confirm-button danger"
                    >
                        
                    </Button>
                )}
            </Modal.Footer>
        </Modal>
    );
};

export default ConfirmDialog; 