import React, { useState, useEffect } from 'react';
import { Modal, Form, Button, Alert } from 'react-bootstrap';
import { booksApi, authorsApi, genresApi } from '../../../lib/supabase';

const CreateBook = ({ show, onHide, onSuccess }) => {
    const [formData, setFormData] = useState({
        title: '',
        author_id: '',
        genre_id: '',
        publication_year: '',
        description: '',
        cover_image: '',
        available_copies: 0,
        total_copies: 0
    });
    const [authors, setAuthors] = useState([]);
    const [genres, setGenres] = useState([]);
    const [error, setError] = useState(null);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        if (show) {
            loadData();
            //      
            setFormData({
                title: '',
                author_id: '',
                genre_id: '',
                publication_year: '',
                description: '',
                cover_image: '',
                available_copies: 0,
                total_copies: 0
            });
        }
    }, [show]);

    const loadData = async () => {
        try {
            setError(null);
            setLoading(true);

            //  
            const { data: authorsData, error: authorsError } = await authorsApi.getAuthors();
            if (authorsError) throw authorsError;
            setAuthors(authorsData || []);

            //  
            const { data: genresData, error: genresError } = await genresApi.getGenres();
            if (genresError) throw genresError;
            setGenres(genresData || []);

        } catch (e) {
            console.error('   :', e);
            setError('   . ,  .');
        } finally {
            setLoading(false);
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError(null);
        setLoading(true);

        try {
            // ,  available_copies   total_copies
            if (parseInt(formData.available_copies) > parseInt(formData.total_copies)) {
                throw new Error('       ');
            }

            const { error: insertError } = await booksApi.createBook({
                title: formData.title,
                author_id: formData.author_id,
                genre_id: formData.genre_id,
                publication_year: parseInt(formData.publication_year),
                description: formData.description,
                cover_image: formData.cover_image,
                available_copies: parseInt(formData.available_copies),
                total_copies: parseInt(formData.total_copies)
            });

            if (insertError) throw insertError;

            onSuccess();
            onHide();
        } catch (e) {
            console.error('   :', e);
            setError(e.message || '   . ,  .');
        } finally {
            setLoading(false);
        }
    };

    return (
        <Modal show={show} onHide={onHide} centered>
            <Modal.Header closeButton>
                <Modal.Title> </Modal.Title>
            </Modal.Header>
            <Form onSubmit={handleSubmit}>
                <Modal.Body>
                    {error && <Alert variant="danger">{error}</Alert>}
                    <Form.Group className="mb-3">
                        <Form.Label> </Form.Label>
                        <Form.Control
                            type="text"
                            value={formData.title}
                            onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                            required
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label></Form.Label>
                        <Form.Select
                            value={formData.author_id || ''}
                            onChange={(e) => setFormData({ ...formData, author_id: e.target.value })}
                            required
                        >
                            <option value=""> </option>
                            {authors.map(author => (
                                <option key={author.id} value={author.id}>
                                    {author.name}
                                </option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label></Form.Label>
                        <Form.Select
                            value={formData.genre_id || ''}
                            onChange={(e) => setFormData({ ...formData, genre_id: e.target.value })}
                            required
                        >
                            <option value=""> </option>
                            {genres.map(genre => (
                                <option key={genre.id} value={genre.id}>
                                    {genre.name}
                                </option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label> </Form.Label>
                        <Form.Control
                            type="number"
                            min="1800"
                            max={new Date().getFullYear()}
                            value={formData.publication_year}
                            onChange={(e) => setFormData({ ...formData, publication_year: e.target.value })}
                            required
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label> </Form.Label>
                        <Form.Control
                            type="number"
                            min="0"
                            value={formData.total_copies}
                            onChange={(e) => setFormData({ ...formData, total_copies: parseInt(e.target.value) || 0 })}
                            required
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label> </Form.Label>
                        <Form.Control
                            type="number"
                            min="0"
                            max={formData.total_copies}
                            value={formData.available_copies}
                            onChange={(e) => setFormData({ ...formData, available_copies: parseInt(e.target.value) || 0 })}
                            required
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>URL </Form.Label>
                        <Form.Control
                            type="url"
                            value={formData.cover_image}
                            onChange={(e) => setFormData({ ...formData, cover_image: e.target.value })}
                            placeholder="https://example.com/image.jpg"
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label></Form.Label>
                        <Form.Control
                            as="textarea"
                            rows={3}
                            value={formData.description}
                            onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                        />
                    </Form.Group>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={onHide}>
                        
                    </Button>
                    <Button variant="primary" type="submit" disabled={loading}>
                        {loading ? '...' : ''}
                    </Button>
                </Modal.Footer>
            </Form>
        </Modal>
    );
};

export default CreateBook; 