import React, { useEffect, useState, useCallback, forwardRef, useImperativeHandle } from 'react';
import { Table, Spinner, Alert, Button, Form } from 'react-bootstrap';
import { ratingsApi } from '../../lib/supabase';
import EditRating from '../modals/Rating/EditRating';
import ConfirmDialog from '../modals/Dialoge/ConfirmDialog';
import CreateRating from '../modals/Rating/CreateRating';
import { loaderStyles } from '../../styles/loaderStyles';
import SortIcon from './SortIcon';

const headerStyle = {
    cursor: 'pointer',
    userSelect: 'none',
    backgroundColor: '#f8f9fa',
    position: 'sticky',
    top: 0,
    zIndex: 1
};

const RatingsTable = forwardRef(({ searchQuery = '' }, ref) => {
    const [ratings, setRatings] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [editRating, setEditRating] = useState(null);
    const [showEditModal, setShowEditModal] = useState(false);
    const [showDeleteDialog, setShowDeleteDialog] = useState(false);
    const [ratingToDelete, setRatingToDelete] = useState(null);
    const [showCreateModal, setShowCreateModal] = useState(false);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(8);
    const [sortField, setSortField] = useState('created_at');
    const [sortDirection, setSortDirection] = useState('desc');

    const handleSort = (field) => {
        if (sortField === field) {
            setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
        } else {
            setSortField(field);
            setSortDirection('asc');
        }
    };

    const loadRatings = useCallback(async () => {
        try {
            setLoading(true);
            const { data, error } = await ratingsApi.getRatings({
                sortField,
                sortDirection,
                searchQuery
            });

            if (error) throw error;
            
            setRatings(data || []);
            setError(null);
        } catch (e) {
            console.error('   :', e);
            setError('   ');
        } finally {
            setLoading(false);
        }
    }, [sortField, sortDirection, searchQuery]);

    useImperativeHandle(ref, () => ({
        loadRatings
    }));

    useEffect(() => {
        loadRatings();

        const handleUpdate = () => {
            loadRatings();
        };

        window.addEventListener('updateRatingsTable', handleUpdate);
        return () => {
            window.removeEventListener('updateRatingsTable', handleUpdate);
        };
    }, [loadRatings]);

    const handleEdit = (rating) => {
        setEditRating(rating);
        setShowEditModal(true);
    };

    const handleDelete = async (rating) => {
        setRatingToDelete(rating);
        setShowDeleteDialog(true);
    };

    const confirmDelete = async () => {
        try {
            const { error: deleteError } = await ratingsApi.deleteRating(ratingToDelete.id);
            if (deleteError) throw deleteError;
            
            await loadRatings();
            setShowDeleteDialog(false);
            setRatingToDelete(null);
        } catch (e) {
            console.error('   :', e);
            setError('   ');
        }
    };

    if (loading && !ratings.length) {
        return (
            <div style={loaderStyles.container}>
                <Spinner animation="border" style={loaderStyles.spinner} />
            </div>
        );
    }

    if (error) {
        return <Alert variant="danger">{error}</Alert>;
    }

    const sortedRatings = [...ratings].sort((a, b) => {
        let aValue, bValue;
        
        switch(sortField) {
            case 'book':
                aValue = a.book?.title || '';
                bValue = b.book?.title || '';
                break;
            case 'user':
                aValue = a.user?.full_name || '';
                bValue = b.user?.full_name || '';
                break;
            case 'rating':
                aValue = Number(a.rating);
                bValue = Number(b.rating);
                break;
            case 'created_at':
                aValue = new Date(a.created_at);
                bValue = new Date(b.created_at);
                break;
            default:
                aValue = '';
                bValue = '';
        }

        if (aValue < bValue) return sortDirection === 'asc' ? -1 : 1;
        if (aValue > bValue) return sortDirection === 'asc' ? 1 : -1;
        return 0;
    });
    
    // 
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = sortedRatings.slice(indexOfFirstItem, indexOfLastItem);
    const totalPages = Math.max(1, Math.ceil(sortedRatings.length / itemsPerPage));

    const pageItemStyle = {
        width: '32px',
        height: '32px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        border: '1px solid #dee2e6',
        borderRadius: '4px',
        cursor: 'pointer',
        backgroundColor: 'white',
        color: '#0d6efd',
        textDecoration: 'none',
        fontSize: '14px'
    };

    const activePageStyle = {
        ...pageItemStyle,
        backgroundColor: '#0d6efd',
        color: 'white',
        border: '1px solid #0d6efd'
    };

    const disabledPageStyle = {
        ...pageItemStyle,
        cursor: 'not-allowed',
        color: '#6c757d',
        backgroundColor: '#e9ecef'
    };

    return (
        <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
            <div style={{ flex: '1', overflowY: 'auto', marginBottom: '20px', display: 'flex', flexDirection: 'column', minHeight: '510px' }}>
                <div className="mb-3 d-flex justify-content-end">
                    <Button 
                        variant="success" 
                        onClick={() => setShowCreateModal(true)}
                        style={{ transition: 'all 0.2s' }}
                        className="text-white"
                    >
                         
                    </Button>
                </div>

                <Table striped bordered hover style={{ tableLayout: 'fixed' }}>
                    <thead>
                        <tr style={{ height: '45px' }}>
                            <th onClick={() => handleSort('book')} style={headerStyle}>
                                 <SortIcon field="book" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('user')} style={headerStyle}>
                                 <SortIcon field="user" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('rating')} style={headerStyle}>
                                 <SortIcon field="rating" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('created_at')} style={headerStyle}>
                                 <SortIcon field="created_at" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th style={{ width: '200px' }}></th>
                        </tr>
                    </thead>
                    <tbody>
                        {currentItems.map(rating => (
                            <tr key={rating.id} style={{ height: '45px' }}>
                                <td>{rating.book?.title}</td>
                                <td>{rating.user?.full_name}</td>
                                <td>{rating.rating}</td>
                                <td>{new Date(rating.created_at).toLocaleDateString()}</td>
                                <td>
                                    <div className="d-flex gap-1">
                                        <Button 
                                            variant="primary" 
                                            size="sm"
                                            onClick={() => handleEdit(rating)}
                                            style={{ padding: '0.2rem 0.5rem', borderRadius: '0.2rem' }}
                                        >
                                            
                                        </Button>
                                        <Button 
                                            variant="danger" 
                                            size="sm"
                                            onClick={() => handleDelete(rating)}
                                            style={{ padding: '0.2rem 0.5rem', borderRadius: '0.2rem' }}
                                        >
                                            
                                        </Button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                        {currentItems.length === 0 && (
                            <tr>
                                <td colSpan="5" className="text-center">
                                    {searchQuery ? '  ' : '  '}
                                </td>
                            </tr>
                        )}
                    </tbody>
                </Table>
            </div>

            <div style={{ position: 'sticky', bottom: 0, backgroundColor: 'white', paddingTop: '10px', borderTop: '1px solid #dee2e6' }} className="d-flex justify-content-center gap-2">
                <button 
                    style={currentPage === 1 ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(1)}
                    disabled={currentPage === 1}
                >
                    
                </button>
                <button 
                    style={currentPage === 1 ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                    disabled={currentPage === 1}
                >
                    
                </button>
                {[...Array(totalPages)].map((_, idx) => {
                    const pageNumber = idx + 1;
                    return (
                        <button
                            key={pageNumber}
                            style={pageNumber === currentPage ? activePageStyle : pageItemStyle}
                            onClick={() => setCurrentPage(pageNumber)}
                        >
                            {pageNumber}
                        </button>
                    );
                })}
                <button 
                    style={currentPage === totalPages ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                    disabled={currentPage === totalPages}
                >
                    
                </button>
                <button 
                    style={currentPage === totalPages ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(totalPages)}
                    disabled={currentPage === totalPages}
                >
                    
                </button>
            </div>

            <EditRating
                show={showEditModal}
                onHide={() => {
                    setShowEditModal(false);
                    setEditRating(null);
                }}
                rating={editRating}
                onSuccess={loadRatings}
            />

            <ConfirmDialog
                show={showDeleteDialog}
                onHide={() => {
                    setShowDeleteDialog(false);
                    setRatingToDelete(null);
                }}
                onConfirm={confirmDelete}
                title=" "
                message={`       "${ratingToDelete?.book?.title}"?`}
            />

            <CreateRating 
                show={showCreateModal} 
                onHide={() => setShowCreateModal(false)}
                onSuccess={loadRatings}
            />
        </div>
    );
});

export default RatingsTable; 