import React, { useState, useEffect } from 'react';
import { Table, Button, Form, InputGroup, Alert, Spinner } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEdit, faTrash } from '@fortawesome/free-solid-svg-icons';
import { stockApi } from '../../lib/supabase';
import SortIcon from './SortIcon';
import './Table.css';

const StockTable = ({ searchQuery, setSearchQuery }) => {
    const [stocks, setStocks] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [sortField, setSortField] = useState('id');
    const [sortDirection, setSortDirection] = useState('asc');
    const [selectedStock, setSelectedStock] = useState(null);
    const [showEditModal, setShowEditModal] = useState(false);
    const [showCreateModal, setShowCreateModal] = useState(false);
    const [showDeleteDialog, setShowDeleteDialog] = useState(false);
    const [deleteError, setDeleteError] = useState(null);

    useEffect(() => {
        fetchStocks();
    }, [sortField, sortDirection]);

    const fetchStocks = async () => {
        try {
            setLoading(true);
            setError(null);

            const { data, error } = await stockApi.getStocks({ 
                sortField, 
                sortDirection,
                includeBooks: true 
            });

            if (error) throw error;

            setStocks(data);
        } catch (err) {
            console.error('     :', err);
            setError('     ');
        } finally {
            setLoading(false);
        }
    };

    const handleSort = (field) => {
        if (field === sortField) {
            setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
        } else {
            setSortField(field);
            setSortDirection('asc');
        }
    };

    const handleDelete = async (stock) => {
        setSelectedStock(stock);
        setShowDeleteDialog(true);
    };

    const handleEdit = (stock) => {
        setSelectedStock(stock);
        setShowEditModal(true);
    };

    const filteredStocks = stocks.filter(stock => {
        const searchLower = searchQuery.toLowerCase();
        return (
            stock.book?.title.toLowerCase().includes(searchLower) ||
            stock.quantity.toString().includes(searchLower)
        );
    });

    if (loading) {
        return (
            <div className="text-center p-4">
                <Spinner animation="border" role="status">
                    <span className="visually-hidden">...</span>
                </Spinner>
            </div>
        );
    }

    if (error) {
        return <Alert variant="danger">{error}</Alert>;
    }

    return (
        <div>
            <div className="d-flex justify-content-between align-items-center mb-4">
                <Button variant="primary" onClick={() => setShowCreateModal(true)}>
                     
                </Button>
                <InputGroup className="w-auto">
                    <Form.Control
                        placeholder="..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                    />
                </InputGroup>
            </div>

            <Table striped bordered hover>
                <thead>
                    <tr>
                        <th onClick={() => handleSort('id')} style={{ cursor: 'pointer' }}>
                            ID <SortIcon field="id" {...{sortField, sortDirection}} />
                        </th>
                        <th onClick={() => handleSort('book_id')} style={{ cursor: 'pointer' }}>
                             <SortIcon field="book_id" {...{sortField, sortDirection}} />
                        </th>
                        <th onClick={() => handleSort('quantity')} style={{ cursor: 'pointer' }}>
                             <SortIcon field="quantity" {...{sortField, sortDirection}} />
                        </th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {filteredStocks.map(stock => (
                        <tr key={stock.id}>
                            <td>{stock.id}</td>
                            <td>{stock.book?.title || '  '}</td>
                            <td>{stock.quantity}</td>
                            <td>
                                <Button
                                    variant="outline-primary"
                                    size="sm"
                                    className="me-2"
                                    onClick={() => handleEdit(stock)}
                                >
                                    <FontAwesomeIcon icon={faEdit} />
                                </Button>
                                <Button
                                    variant="outline-danger"
                                    size="sm"
                                    onClick={() => handleDelete(stock)}
                                >
                                    <FontAwesomeIcon icon={faTrash} />
                                </Button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </Table>

            {/* TODO:     ,    */}
        </div>
    );
};

export default StockTable; 