import React, { useEffect, useState, useCallback, forwardRef, useImperativeHandle } from 'react';
import { Table, Spinner, Alert, Button, Form } from 'react-bootstrap';
import { loansApi, booksApi, usersApi } from '../../lib/supabase';
import { loaderStyles } from '../../styles/loaderStyles';
import SortIcon from './SortIcon';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEdit, faTrash } from '@fortawesome/free-solid-svg-icons';
import './Table.css';

const headerStyle = {
    cursor: 'pointer',
    userSelect: 'none',
    backgroundColor: '#f8f9fa',
    position: 'sticky',
    top: 0,
    zIndex: 1
};

const LoansTable = forwardRef(({ searchQuery = '' }, ref) => {
    const [loans, setLoans] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [editLoan, setEditLoan] = useState(null);
    const [showEditModal, setShowEditModal] = useState(false);
    const [showDeleteDialog, setShowDeleteDialog] = useState(false);
    const [loanToDelete, setLoanToDelete] = useState(null);
    const [showCreateModal, setShowCreateModal] = useState(false);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(8);
    const [sortField, setSortField] = useState('loan_date');
    const [sortDirection, setSortDirection] = useState('desc');

    const handleSort = (field) => {
        if (sortField === field) {
            setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
        } else {
            setSortField(field);
            setSortDirection('asc');
        }
    };

    const loadLoans = useCallback(async () => {
        try {
            setLoading(true);
            setError(null);

            const { data, error } = await loansApi.getLoans({
                sortField,
                sortDirection,
                includeBooks: true,
                includeUsers: true
            });

            if (error) throw error;
            setLoans(data || []);
        } catch (error) {
            console.error('   :', error);
            setError('    ');
        } finally {
            setLoading(false);
        }
    }, [sortField, sortDirection]);

    useImperativeHandle(ref, () => ({
        loadLoans
    }));

    useEffect(() => {
        loadLoans();
    }, [loadLoans]);

    const handleEdit = (loan) => {
        setEditLoan(loan);
        setShowEditModal(true);
    };

    const handleDelete = async (loan) => {
        setLoanToDelete(loan);
        setShowDeleteDialog(true);
    };

    const confirmDelete = async () => {
        try {
            const { error: deleteError } = await loansApi.deleteLoan(loanToDelete.id);
            if (deleteError) throw deleteError;
            
            await loadLoans();
            setShowDeleteDialog(false);
            setLoanToDelete(null);
        } catch (error) {
            console.error('   :', error);
            setError('   ');
        }
    };

    const filteredLoans = loans.filter(loan => {
        const searchLower = searchQuery.toLowerCase();
        return (
            loan.book?.title.toLowerCase().includes(searchLower) ||
            loan.user?.full_name.toLowerCase().includes(searchLower) ||
            loan.status.toLowerCase().includes(searchLower)
        );
    });

    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredLoans.slice(indexOfFirstItem, indexOfLastItem);
    const totalPages = Math.max(1, Math.ceil(filteredLoans.length / itemsPerPage));

    const pageItemStyle = {
        width: '32px',
        height: '32px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        border: '1px solid #dee2e6',
        borderRadius: '4px',
        cursor: 'pointer',
        backgroundColor: 'white',
        color: '#0d6efd',
        textDecoration: 'none',
        fontSize: '14px'
    };

    const activePageStyle = {
        ...pageItemStyle,
        backgroundColor: '#0d6efd',
        color: 'white',
        border: '1px solid #0d6efd'
    };

    const disabledPageStyle = {
        ...pageItemStyle,
        cursor: 'not-allowed',
        color: '#6c757d',
        backgroundColor: '#e9ecef'
    };

    if (loading && !loans.length) {
        return (
            <div style={loaderStyles.container}>
                <Spinner animation="border" style={loaderStyles.spinner} />
            </div>
        );
    }

    if (error) {
        return <Alert variant="danger">{error}</Alert>;
    }

    return (
        <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
            <div style={{ flex: '1', overflowY: 'auto', marginBottom: '20px', display: 'flex', flexDirection: 'column', minHeight: '510px' }}>
                <div className="mb-3 d-flex justify-content-end">
                    <Button 
                        variant="success" 
                        onClick={() => setShowCreateModal(true)}
                        style={{ transition: 'all 0.2s' }}
                        className="text-white"
                    >
                         
                    </Button>
                </div>

                <Table striped bordered hover style={{ tableLayout: 'fixed' }}>
                    <thead>
                        <tr style={{ height: '45px' }}>
                            <th onClick={() => handleSort('book')} style={headerStyle}>
                                 <SortIcon field="book" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('user')} style={headerStyle}>
                                 <SortIcon field="user" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('loan_date')} style={headerStyle}>
                                  <SortIcon field="loan_date" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('due_date')} style={headerStyle}>
                                  <SortIcon field="due_date" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('status')} style={headerStyle}>
                                 <SortIcon field="status" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th style={{ width: '200px' }}></th>
                        </tr>
                    </thead>
                    <tbody>
                        {currentItems.map(loan => (
                            <tr key={loan.id} style={{ height: '45px' }}>
                                <td>{loan.book?.title}</td>
                                <td>{loan.user?.full_name}</td>
                                <td>{new Date(loan.loan_date).toLocaleDateString()}</td>
                                <td>{loan.due_date ? new Date(loan.due_date).toLocaleDateString() : '-'}</td>
                                <td>{loan.status}</td>
                                <td>
                                    <div className="d-flex gap-1">
                                        <Button 
                                            variant="primary" 
                                            size="sm"
                                            onClick={() => handleEdit(loan)}
                                            style={{ padding: '0.2rem 0.5rem', borderRadius: '0.2rem' }}
                                        >
                                            <FontAwesomeIcon icon={faEdit} />
                                        </Button>
                                        <Button 
                                            variant="danger" 
                                            size="sm"
                                            onClick={() => handleDelete(loan)}
                                            style={{ padding: '0.2rem 0.5rem', borderRadius: '0.2rem' }}
                                        >
                                            <FontAwesomeIcon icon={faTrash} />
                                        </Button>
                                    </div>
                                </td>
                            </tr>
                        ))}
                        {currentItems.length === 0 && (
                            <tr>
                                <td colSpan="6" className="text-center">
                                    {searchQuery ? '  ' : ' '}
                                </td>
                            </tr>
                        )}
                    </tbody>
                </Table>
            </div>

            <div style={{ position: 'sticky', bottom: 0, backgroundColor: 'white', paddingTop: '10px', borderTop: '1px solid #dee2e6' }} className="d-flex justify-content-center gap-2">
                <button 
                    style={currentPage === 1 ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(1)}
                    disabled={currentPage === 1}
                >
                    
                </button>
                <button 
                    style={currentPage === 1 ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                    disabled={currentPage === 1}
                >
                    
                </button>
                {[...Array(totalPages)].map((_, idx) => {
                    const pageNumber = idx + 1;
                    return (
                        <button
                            key={pageNumber}
                            style={pageNumber === currentPage ? activePageStyle : pageItemStyle}
                            onClick={() => setCurrentPage(pageNumber)}
                        >
                            {pageNumber}
                        </button>
                    );
                })}
                <button 
                    style={currentPage === totalPages ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                    disabled={currentPage === totalPages}
                >
                    
                </button>
                <button 
                    style={currentPage === totalPages ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(totalPages)}
                    disabled={currentPage === totalPages}
                >
                    
                </button>
            </div>
        </div>
    );
});

export default LoansTable; 