import { makeAutoObservable, action } from 'mobx';
import { auth } from '../lib/supabase';

export default class UserStore {
    constructor() {
        this._user = {
            id: null,
            email: '',
            role: 'USER'
        };
        this._isAuth = false;
        this.usersTableRef = null;

        makeAutoObservable(this, {
            setIsAuth: action,
            setUser: action,
            logout: action,
            setUsersTableRef: action
        });

        //    
        this.checkSession();

        //    
        auth.onAuthStateChange(action((event, session) => {
            if (event === 'SIGNED_IN') {
                this.setIsAuth(true);
                this.setUser(session.user);
            } else if (event === 'SIGNED_OUT') {
                this.setIsAuth(false);
                this.setUser({
                    id: null,
                    email: '',
                    role: 'USER'
                });
            }
        }));
    }

    async checkSession() {
        try {
            const { session, user } = await auth.getSession();
            if (session && user) {
                this.setIsAuth(true);
                this.setUser(user);
            } else {
                this.setIsAuth(false);
                this.setUser(null);
            }
        } catch (error) {
            console.error('Check session error:', error);
            this.setIsAuth(false);
            this.setUser(null);
        }
    }

    setIsAuth(bool) {
        this._isAuth = bool;
    }

    setUser(user) {
        this._user = user || {
            id: null,
            email: '',
            role: 'USER'
        };
    }

    get isAuth() {
        return this._isAuth;
    }

    get user() {
        return this._user;
    }

    get role() {
        return this._user?.user_metadata?.role || 'USER';
    }

    get username() {
        return this._user?.user_metadata?.username || this._user?.email;
    }

    get isAdmin() {
        return this._user.role === 'ADMIN';
    }

    async logout() {
        try {
            await auth.signOut();
            this.setIsAuth(false);
            this.setUser({
                id: null,
                email: '',
                role: 'USER'
            });
        } catch (error) {
            console.error('Logout error:', error);
            throw error;
        }
    }

    setUsersTableRef = (ref) => {
        this.usersTableRef = ref;
    }

    async login(email, password) {
        try {
            const { data, error } = await auth.signIn(email, password);
            if (error) throw error;
            
            if (data?.user) {
                this.setUser(data.user);
                this.setIsAuth(true);
            }
        } catch (error) {
            console.error('Login error:', error);
            this.setIsAuth(false);
            this.setUser(null);
            throw error;
        }
    }
}