import React, { useContext, useEffect, useState } from 'react';
import { Context } from '../index';
import { NavLink, useNavigate } from 'react-router-dom';
import { Navbar, Nav, Container, Button, Dropdown } from 'react-bootstrap';
import { observer } from 'mobx-react-lite';
import { useAuth } from '../contexts/AuthContext';
import { auth } from '../lib/supabase';
import { ADMIN_ROUTE, LOGIN_ROUTE, SHOP_ROUTE, PROFILE_ROUTE } from '../utils/consts';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUser } from '@fortawesome/free-solid-svg-icons';
import ProfileModal from './modals/Profile/ProfileModal';
import '../styles/NavBar.css';
import logo from '../assets/logo.svg';
import { PersonCircle, CaretDownFill } from 'react-bootstrap-icons';

const NavBar = observer(() => {
    const { user, signOut, loading } = useAuth();
    const navigate = useNavigate();
    const [avatarUrl, setAvatarUrl] = useState(null);

    console.log('NavBar  -  :', user);
    console.log('NavBar  -  :', loading);

    useEffect(() => {
        if (user?.id) {
            console.log('   :', user.id);
            fetchAvatarUrl();
        } else {
            console.log('    ');
            setAvatarUrl(null);
        }
    }, [user]);

    const fetchAvatarUrl = async () => {
        try {
            console.log(' URL   ...');
            const { data, error } = await auth
                .from('users')
                .select('avatar_url')
                .eq('id', user.id)
                .single();

            if (error) throw error;
            
            if (data?.avatar_url) {
                console.log(' URL :', data.avatar_url);
                const { data: { publicUrl } } = auth
                    .storage
                    .from('avatars')
                    .getPublicUrl(data.avatar_url);
                console.log(' URL :', publicUrl);
                setAvatarUrl(publicUrl);
            } else {
                console.log('URL   ');
                setAvatarUrl(null);
            }
        } catch (error) {
            console.error('   :', error);
            setAvatarUrl(null);
        }
    };

    const handleLogout = async () => {
        try {
            console.log('  ...');
            await signOut();
            console.log('  ');
        } catch (error) {
            console.error('  :', error);
        }
    };

    const renderNavbar = () => (
        <Navbar bg="dark" variant="dark" className="navbar-custom">
            <Container>
                <NavLink className="navbar-brand" to="/">
                    <img src={logo} alt="" className="navbar-logo" />
                    <span className="brand-text"> </span>
                </NavLink>
                <Nav className="ms-auto">
                    <div className="d-flex align-items-center">
                        {user ? (
                            <>
                                <span className="user-email me-3">
                                    {user.email}
                                </span>
                                <Dropdown align="end">
                                    <Dropdown.Toggle variant="link" id="dropdown-profile" className="profile-toggle">
                                        <div className="d-flex align-items-center">
                                            {avatarUrl ? (
                                                <img
                                                    src={avatarUrl}
                                                    alt=""
                                                    className="avatar-image"
                                                />
                                            ) : (
                                                <div className="person-circle-container">
                                                    <PersonCircle size={40} />
                                                </div>
                                            )}
                                            <CaretDownFill size={12} className="ms-2 dropdown-caret" />
                                        </div>
                                    </Dropdown.Toggle>

                                    <Dropdown.Menu variant="dark">
                                        <Dropdown.Item onClick={() => navigate(PROFILE_ROUTE)}>
                                            
                                        </Dropdown.Item>
                                        {(user.role === 'LIBRARIAN' || user.role === 'ADMIN' || user.role === 'SUPERADMIN') && (
                                            <Dropdown.Item onClick={() => navigate('/admin')}>
                                                 
                                            </Dropdown.Item>
                                        )}
                                        <Dropdown.Divider />
                                        <Dropdown.Item onClick={handleLogout}>
                                            
                                        </Dropdown.Item>
                                    </Dropdown.Menu>
                                </Dropdown>
                            </>
                        ) : (
                            <div className="d-flex align-items-center">
                                <div 
                                    className="person-circle-container" 
                                    onClick={() => navigate('/auth')}
                                >
                                    <PersonCircle size={32} />
                                </div>
                                <button className="auth-button ms-3" onClick={() => navigate('/auth')}>
                                    
                                </button>
                            </div>
                        )}
                    </div>
                </Nav>
            </Container>
        </Navbar>
    );

    return loading ? (
        <Navbar bg="dark" variant="dark" className="navbar-custom">
            <Container>
                <NavLink className="navbar-brand" to="/">
                    <img src={logo} alt="" className="navbar-logo" />
                    <span className="brand-text"> </span>
                </NavLink>
                <Nav className="ms-auto">
                    <div className="d-flex align-items-center">
                        <span className="text-light">...</span>
                    </div>
                </Nav>
            </Container>
        </Navbar>
    ) : renderNavbar();
});

export default NavBar; 