import React, { useState, useRef, useEffect } from 'react';
import { Form, Button, Alert } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { ArrowLeft } from 'react-bootstrap-icons';
import supabase, { createUserAfterVerification } from '../lib/supabase';
import '../styles/EmailVerification.css';

const EmailVerification = ({ email, onVerificationComplete, userData, onBack }) => {
    const [verificationCode, setVerificationCode] = useState(['', '', '', '', '', '']);
    const [error, setError] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [resendTimer, setResendTimer] = useState(60);
    const [progress, setProgress] = useState(100);
    const inputRefs = useRef([]);
    const navigate = useNavigate();

    useEffect(() => {
        inputRefs.current[0]?.focus();
    }, []);

    useEffect(() => {
        let timer;
        if (resendTimer > 0) {
            timer = setInterval(() => {
                setResendTimer(prev => prev - 1);
                setProgress(prev => (resendTimer - 1) * (100/60));
            }, 1000);
        } else {
            setProgress(100);
        }
        return () => clearInterval(timer);
    }, [resendTimer]);

    const handleResendCode = async () => {
        try {
            setIsLoading(true);
            setError('');
            
            const { error: otpError } = await supabase.auth.signInWithOtp({
                email,
                options: {
                    shouldCreateUser: false
                }
            });

            if (otpError) throw otpError;

            setResendTimer(60);
        } catch (error) {
            console.error('Error resending code:', error);
            setError('   .  .');
        } finally {
            setIsLoading(false);
        }
    };

    const handleInputChange = (index, value) => {
        if (value.length > 1) return; //     

        const newCode = [...verificationCode];
        newCode[index] = value;
        setVerificationCode(newCode);

        //     
        if (value && index < 5) {
            inputRefs.current[index + 1]?.focus();
        }
    };

    const handleKeyDown = (index, e) => {
        //  backspace
        if (e.key === 'Backspace' && !verificationCode[index] && index > 0) {
            inputRefs.current[index - 1]?.focus();
        }
    };

    const handlePaste = (e) => {
        e.preventDefault();
        const pastedData = e.clipboardData.getData('text').slice(0, 6);
        if (pastedData.length === 6) {
            const newCode = pastedData.split('');
            setVerificationCode(newCode);
            inputRefs.current[5]?.focus();
        }
    };

    const handleSubmit = async () => {
        const code = verificationCode.join('');
        if (code.length !== 6) {
            setError(',   ');
            return;
        }

        setIsLoading(true);
        setError('');

        try {
            //   : signup  , email  
            const verificationType = userData ? 'signup' : 'email';
            
            const { data, error } = await supabase.auth.verifyOtp({
                email,
                token: code,
                type: verificationType
            });

            if (error) throw error;

            if (data?.user) {
                if (userData) {
                    //           
                    const { error: createError } = await createUserAfterVerification(data.user.id, userData);
                    if (createError) throw createError;
                }
                
                onVerificationComplete(data.user);
            }
        } catch (error) {
            console.error('Verification error:', error);
            setError('  ');
        } finally {
            setIsLoading(false);
        }
    };

    const isCodeComplete = verificationCode.every(digit => digit !== '');

    return (
        <div className="verification-container">
            <button className="back-button" onClick={onBack}>
                <ArrowLeft size={20} />
            </button>
            <h2> Email</h2>
            <p>     {email}</p>

            {error && (
                <Alert variant="danger" className="mb-3">
                    {error}
                </Alert>
            )}

            <div className="verification-inputs" onPaste={handlePaste}>
                {verificationCode.map((digit, index) => (
                    <input
                        key={index}
                        ref={el => inputRefs.current[index] = el}
                        type="text"
                        maxLength={1}
                        value={digit}
                        onChange={(e) => handleInputChange(index, e.target.value)}
                        onKeyDown={(e) => handleKeyDown(index, e)}
                        className="verification-input"
                    />
                ))}
            </div>

            <Button
                variant="primary"
                onClick={handleSubmit}
                disabled={!isCodeComplete || isLoading}
                className="verification-button mb-3"
            >
                {isLoading ? '...' : ''}
            </Button>

            <div className="text-center">
                {resendTimer > 0 ? (
                    <>
                        <p className="resend-timer">
                                 {resendTimer} {resendTimer === 1 ? '' : resendTimer < 5 ? '' : ''}
                        </p>
                        <div className="countdown-container">
                            <div 
                                className="countdown-progress" 
                                style={{ width: `${progress}%` }}
                            />
                        </div>
                    </>
                ) : (
                    <Button
                        variant="link"
                        onClick={handleResendCode}
                        disabled={isLoading}
                        className="resend-button"
                    >
                          
                    </Button>
                )}
            </div>
        </div>
    );
};

export default EmailVerification; 