import React, { useEffect, useState, useCallback, forwardRef, useImperativeHandle, useContext } from 'react';
import { Table, Spinner, Alert, Button, Badge, Form, InputGroup } from 'react-bootstrap';
import { usersApi, ratingsApi } from '../../lib/supabase';
import { useAuth } from '../../contexts/AuthContext';
import { StoreContext } from '../../contexts/StoreContext';
import { loaderStyles } from '../../styles/loaderStyles';
import ConfirmDialog from '../modals/Dialoge/ConfirmDialog';
import SortIcon from './SortIcon';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEdit, faTrash } from '@fortawesome/free-solid-svg-icons';
import './Table.css';

const headerStyle = {
    cursor: 'pointer',
    userSelect: 'none',
    backgroundColor: '#f8f9fa',
    position: 'sticky',
    top: 0,
    zIndex: 1
};

const UsersTable = forwardRef(({ searchQuery = '', setSearchQuery }, ref) => {
    const { user: authUser } = useAuth();
    const store = useContext(StoreContext);
    const [users, setUsers] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [showDeleteDialog, setShowDeleteDialog] = useState(false);
    const [userToDelete, setUserToDelete] = useState(null);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(8);
    const [activeUsers, setActiveUsers] = useState(new Set());
    const [sortField, setSortField] = useState('created_at');
    const [sortDirection, setSortDirection] = useState('desc');

    const handleSort = (field) => {
        if (field === sortField) {
            setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
        } else {
            setSortField(field);
            setSortDirection('asc');
        }
    };

    const loadUsers = async () => {
        try {
            setLoading(true);
            setError(null);

            const { data: usersData, error: usersError } = await usersApi.getUsers({
                sortField,
                sortDirection
            });

            if (usersError) throw usersError;

            //  email 
            const { data: authData, error: authError } = await usersApi.getUserEmails();
            if (authError) throw authError;

            //    
            const presenceState = await usersApi.getPresenceState();
            
            //    
            const usersWithStatus = usersData.map(user => ({
                ...user,
                email: authData.find(auth => auth.id === user.id)?.email || '',
                isOnline: Object.values(presenceState).some(presence => 
                    presence.some(p => p.user_id === user.id)
                )
            }));

            setUsers(usersWithStatus);
        } catch (err) {
            console.error('   :', err);
            setError('   ');
        } finally {
            setLoading(false);
        }
    };

    useImperativeHandle(ref, () => ({
        refreshTable: loadUsers
    }), []);

    useEffect(() => {
        loadUsers();
        //    
        const unsubscribe = usersApi.subscribeToPresence(() => {
            loadUsers();
        });

        return () => {
            unsubscribe();
        };
    }, [sortField, sortDirection]);

    const canDeleteUser = (userToDelete) => {
        return userToDelete.role !== 'SUPERADMIN' && userToDelete.id !== authUser.id;
    };

    const handleDelete = async (user) => {
        if (!canDeleteUser(user)) {
            setError(user.id === authUser.id 
                ? '      ' 
                : '     SUPERADMIN');
            return;
        }

        setUserToDelete(user);
        setShowDeleteDialog(true);
    };

    const confirmDelete = async () => {
        try {
            if (!canDeleteUser(userToDelete)) {
                setError('     ');
                return;
            }

            setLoading(true);

            //    
            const { data: ratings, error: ratingsError } = await ratingsApi.getUserRatings(userToDelete.id);
            if (ratingsError) throw ratingsError;

            if (ratings && ratings.length > 0) {
                //   
                await ratingsApi.deleteUserRatings(userToDelete.id);
            }

            //  
            const { error: deleteError } = await usersApi.deleteUser(userToDelete.id);
            if (deleteError) throw deleteError;
            
            setShowDeleteDialog(false);
            setUserToDelete(null);
            setError(null);
            
            //  
            await loadUsers();
        } catch (e) {
            console.error('   :', e);
            setError('   : ' + e.message);
        } finally {
            setLoading(false);
        }
    };

    const getSortedAndFilteredUsers = () => {
        const filteredUsers = users.filter(user => {
            const searchLower = searchQuery.toLowerCase();
            return (
                (user.email?.toLowerCase() || '').includes(searchLower) ||
                (user.role?.toLowerCase() || '').includes(searchLower) ||
                ((user.first_name || '') + ' ' + (user.last_name || '') + ' ' + (user.patronymic || '')).toLowerCase().includes(searchLower)
            );
        });

        return filteredUsers.sort((a, b) => {
            let aValue = a[sortField] || '';
            let bValue = b[sortField] || '';

            if (aValue < bValue) return sortDirection === 'asc' ? -1 : 1;
            if (aValue > bValue) return sortDirection === 'asc' ? 1 : -1;
            return 0;
        });
    };

    const getRoleColor = (role) => {
        switch(role) {
            case 'SUPERADMIN': return 'text-danger fw-bold';
            case 'ADMIN': return 'text-success fw-bold';
            default: return 'text-primary';
        }
    };

    const sortedAndFilteredUsers = getSortedAndFilteredUsers();
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = sortedAndFilteredUsers.slice(indexOfFirstItem, indexOfLastItem);
    const totalPages = Math.max(1, Math.ceil(sortedAndFilteredUsers.length / itemsPerPage));

    const pageItemStyle = {
        width: '32px',
        height: '32px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        border: '1px solid #dee2e6',
        borderRadius: '4px',
        cursor: 'pointer',
        backgroundColor: 'white',
        color: '#0d6efd',
        textDecoration: 'none',
        fontSize: '14px'
    };

    const activePageStyle = {
        ...pageItemStyle,
        backgroundColor: '#0d6efd',
        color: 'white',
        border: '1px solid #0d6efd'
    };

    const disabledPageStyle = {
        ...pageItemStyle,
        cursor: 'not-allowed',
        color: '#6c757d',
        backgroundColor: '#e9ecef'
    };

    if (loading && !users.length) {
        return (
            <div style={loaderStyles.container}>
                <Spinner animation="border" style={loaderStyles.spinner} />
            </div>
        );
    }

    if (error) {
        return <Alert variant="danger">{error}</Alert>;
    }

    return (
        <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
            <div style={{ flex: '1', overflowY: 'auto', marginBottom: '20px', display: 'flex', flexDirection: 'column', minHeight: '510px' }}>
                <Table striped bordered hover style={{ tableLayout: 'fixed' }}>
                    <thead>
                        <tr style={{ height: '45px' }}>
                            <th onClick={() => handleSort('first_name')} style={headerStyle}>
                                 <SortIcon field="first_name" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('last_name')} style={headerStyle}>
                                 <SortIcon field="last_name" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('patronymic')} style={headerStyle}>
                                 <SortIcon field="patronymic" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('email')} style={headerStyle}>
                                Email <SortIcon field="email" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('role')} style={headerStyle}>
                                 <SortIcon field="role" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th onClick={() => handleSort('created_at')} style={headerStyle}>
                                  <SortIcon field="created_at" currentField={sortField} direction={sortDirection} />
                            </th>
                            <th style={{ width: '100px' }}></th>
                            <th style={{ width: '100px' }}></th>
                        </tr>
                    </thead>
                    <tbody>
                        {currentItems.map(user => (
                            <tr key={user.id} style={{ height: '45px' }}>
                                <td>{user.first_name || ''}</td>
                                <td>{user.last_name || ''}</td>
                                <td>{user.patronymic || ''}</td>
                                <td>{user.email || ''}</td>
                                <td>
                                    <span className={getRoleColor(user.role)}>
                                        {user.role || ''}
                                    </span>
                                </td>
                                <td>{user.created_at ? new Date(user.created_at).toLocaleDateString() : ''}</td>
                                <td>
                                    {user.isOnline ? (
                                        <Badge bg="success"></Badge>
                                    ) : (
                                        <Badge bg="secondary"></Badge>
                                    )}
                                </td>
                                <td>
                                    {user.id !== store.user.user?.id && user.role !== 'SUPERADMIN' && (
                                        <Button 
                                            variant="danger" 
                                            size="sm"
                                            onClick={() => handleDelete(user)}
                                            style={{ padding: '0.2rem 0.5rem', borderRadius: '0.2rem' }}
                                        >
                                            
                                        </Button>
                                    )}
                                </td>
                            </tr>
                        ))}
                        {currentItems.length === 0 && (
                            <tr>
                                <td colSpan="7" className="text-center">
                                    {searchQuery ? '  ' : '  '}
                                </td>
                            </tr>
                        )}
                    </tbody>
                </Table>
            </div>

            <div style={{ position: 'sticky', bottom: 0, backgroundColor: 'white', paddingTop: '10px', borderTop: '1px solid #dee2e6' }} className="d-flex justify-content-center gap-2">
                <button 
                    style={currentPage === 1 ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(1)}
                    disabled={currentPage === 1}
                >
                    
                </button>
                <button 
                    style={currentPage === 1 ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
                    disabled={currentPage === 1}
                >
                    
                </button>
                {[...Array(totalPages)].map((_, idx) => {
                    const pageNumber = idx + 1;
                    return (
                        <button
                            key={pageNumber}
                            style={pageNumber === currentPage ? activePageStyle : pageItemStyle}
                            onClick={() => setCurrentPage(pageNumber)}
                        >
                            {pageNumber}
                        </button>
                    );
                })}
                <button 
                    style={currentPage === totalPages ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
                    disabled={currentPage === totalPages}
                >
                    
                </button>
                <button 
                    style={currentPage === totalPages ? disabledPageStyle : pageItemStyle}
                    onClick={() => setCurrentPage(totalPages)}
                    disabled={currentPage === totalPages}
                >
                    
                </button>
            </div>

            <ConfirmDialog
                show={showDeleteDialog}
                onHide={() => {
                    setShowDeleteDialog(false);
                    setUserToDelete(null);
                }}
                onConfirm={confirmDelete}
                title=" "
                message={`     "${userToDelete?.first_name} ${userToDelete?.last_name} ${userToDelete?.patronymic}"?`}
            />
        </div>
    );
});

export default UsersTable; 