import React, { useState, useEffect } from 'react';
import { Nav, Spinner, Alert } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faUser,
    faBook,
    faStar,
    faHistory,
    faCog,
    faChevronLeft
} from '@fortawesome/free-solid-svg-icons';
import { PersonCircle, PencilFill } from 'react-bootstrap-icons';
import './Profile.css';

const Profile = () => {
    const { user, loading } = useAuth();
    const navigate = useNavigate();
    const [error, setError] = useState('');
    const [activeTab, setActiveTab] = useState('info');
    const [isCollapsed, setIsCollapsed] = useState(false);

    useEffect(() => {
        if (!loading && !user) {
            setError(',       ');
            setTimeout(() => navigate('/auth'), 2000);
        }
    }, [user, loading, navigate]);

    if (loading) {
        return (
            <div className="d-flex justify-content-center align-items-center" style={{ minHeight: '100vh' }}>
                <Spinner animation="border" role="status">
                    <span className="visually-hidden">...</span>
                </Spinner>
            </div>
        );
    }

    if (error) {
        return (
            <div className="container mt-4">
                <Alert variant="danger">{error}</Alert>
            </div>
        );
    }

    if (!user) {
        return null;
    }

    const handleTabChange = (tab) => {
        setActiveTab(tab);
    };

    return (
        <div className="profile-container">
            <div className={`profile-nav ${isCollapsed ? 'collapsed' : ''}`}>
                <div className="nav-header">
                    <h3></h3>
                    <button 
                        className="nav-toggle"
                        onClick={() => setIsCollapsed(!isCollapsed)}
                    >
                        <FontAwesomeIcon 
                            icon={faChevronLeft} 
                            className={`icon ${isCollapsed ? 'collapsed' : ''}`}
                        />
                    </button>
                </div>
                <div className="nav-links">
                    <Nav.Link
                        className={activeTab === 'info' ? 'active' : ''}
                        onClick={() => handleTabChange('info')}
                    >
                        <FontAwesomeIcon icon={faUser} className="me-2" />
                        <span> </span>
                    </Nav.Link>
                    <Nav.Link
                        className={activeTab === 'books' ? 'active' : ''}
                        onClick={() => handleTabChange('books')}
                    >
                        <FontAwesomeIcon icon={faBook} className="me-2" />
                        <span> </span>
                    </Nav.Link>
                    <Nav.Link
                        className={activeTab === 'ratings' ? 'active' : ''}
                        onClick={() => handleTabChange('ratings')}
                    >
                        <FontAwesomeIcon icon={faStar} className="me-2" />
                        <span> </span>
                    </Nav.Link>
                    <Nav.Link
                        className={activeTab === 'history' ? 'active' : ''}
                        onClick={() => handleTabChange('history')}
                    >
                        <FontAwesomeIcon icon={faHistory} className="me-2" />
                        <span></span>
                    </Nav.Link>
                    <Nav.Link
                        className={activeTab === 'settings' ? 'active' : ''}
                        onClick={() => handleTabChange('settings')}
                    >
                        <FontAwesomeIcon icon={faCog} className="me-2" />
                        <span></span>
                    </Nav.Link>
                </div>
            </div>
            <div className={`profile-content ${isCollapsed ? 'expanded' : ''}`}>
                <div className="content-body">
                    {activeTab === 'info' && (
                        <div className="profile-info-container">
                            <h3> </h3>
                            <div className="avatar-section">
                                <div className="avatar-icon-container">
                                    <PersonCircle className="avatar-icon" />
                                </div>
                                <button className="edit-avatar-btn">
                                    <PencilFill className="edit-icon" />
                                    
                                </button>
                            </div>
                            <div className="profile-details">
                                <p>Email: {user.email}</p>
                                <p>: {user.first_name} {user.last_name} {user.middle_name}</p>
                                {user.role !== 'librarian' && <p>: {user.class}</p>}
                            </div>
                        </div>
                    )}
                    {activeTab === 'books' && (
                        <div>
                            <h3> </h3>
                            {/*       */}
                        </div>
                    )}
                    {activeTab === 'ratings' && (
                        <div>
                            <h3> </h3>
                            {/*       */}
                        </div>
                    )}
                    {activeTab === 'history' && (
                        <div>
                            <h3></h3>
                            {/*       */}
                        </div>
                    )}
                    {activeTab === 'settings' && (
                        <div>
                            <h3></h3>
                            {/*       */}
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

export default Profile; 