import { makeAutoObservable } from 'mobx';
import { booksApi, authorsApi, genresApi } from '../lib/supabase';

export default class BookStore {
    constructor() {
        this._books = [];
        this._authors = [];
        this._genres = [];
        this._selectedAuthor = null;
        this._selectedGenre = null;
        this._selectedBook = null;
        this._page = 1;
        this._totalCount = 0;
        this._limit = 12;
        makeAutoObservable(this);
    }

    async fetchAuthors() {
        const { data } = await authorsApi.getAuthors({
            sortField: 'name',
            sortDirection: 'asc'
        });
        this.setAuthors(data || []);
    }

    async fetchGenres() {
        const { data } = await genresApi.getGenres({
            sortField: 'name',
            sortDirection: 'asc'
        });
        this.setGenres(data || []);
    }

    async fetchBooks() {
        const { data } = await booksApi.getBooks({
            sortField: 'title',
            sortDirection: 'asc'
        });
        this.setBooks(data || []);
    }

    setAuthors(authors) {
        this._authors = authors;
    }

    setGenres(genres) {
        this._genres = genres;
    }

    setBooks(books) {
        this._books = books;
        this._totalCount = books.length;
    }

    setSelectedAuthor(author) {
        this.setPage(1);
        this._selectedAuthor = author;
    }

    setSelectedGenre(genre) {
        this.setPage(1);
        this._selectedGenre = genre;
    }

    setSelectedBook(book) {
        this._selectedBook = book;
    }

    setPage(page) {
        this._page = page;
    }

    setTotalCount(count) {
        this._totalCount = count;
    }

    get authors() {
        return this._authors;
    }

    get genres() {
        return this._genres;
    }

    get books() {
        return this._books;
    }

    get selectedAuthor() {
        return this._selectedAuthor;
    }

    get selectedGenre() {
        return this._selectedGenre;
    }

    get selectedBook() {
        return this._selectedBook;
    }

    get totalCount() {
        return this._totalCount;
    }

    get page() {
        return this._page;
    }

    get limit() {
        return this._limit;
    }
} 