import { createClient } from '@supabase/supabase-js';

const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
const supabaseKey = process.env.REACT_APP_SUPABASE_ANON_KEY;

//   
console.log('Supabase Configuration:');
console.log('URL:', supabaseUrl);
console.log('Key exists:', !!supabaseKey);

if (!supabaseUrl || !supabaseKey) {
    console.error('Missing environment variables:');
    console.error('SUPABASE_URL:', supabaseUrl);
    console.error('SUPABASE_KEY exists:', !!supabaseKey);
    throw new Error('Missing Supabase environment variables');
}

//   Supabase   
const supabase = createClient(supabaseUrl, supabaseKey, {
    auth: {
        autoRefreshToken: true,
        persistSession: true,
        detectSessionInUrl: true
    },
    global: {
        headers: {
            'x-client-info': 'supabase-js-client',
        },
    },
});

//  
const testConnection = async () => {
    try {
        const { error } = await supabase.from('users').select('count', { count: 'exact', head: true });
        if (error) {
            console.error('Supabase connection test failed:', error);
        } else {
            console.log('Supabase connection test successful');
        }
    } catch (error) {
        console.error('Supabase connection test error:', error);
    }
};

testConnection();

// API  
const auth = {
    signUp: async (email, password) => {
        try {
            console.log('Attempting to sign up user:', email);
            const { data, error } = await supabase.auth.signUp({
                email,
                password,
            });
            
            if (error) {
                console.error('Sign up error:', error);
                throw error;
            }
            
            console.log('Sign up successful:', data);
            return { data, error: null };
        } catch (error) {
            console.error('Sign up error:', error);
            return { data: null, error };
        }
    },

    signIn: async (email, password) => {
        try {
            //   
            const { data: userExists } = await supabase
                .from('users')
                .select('id')
                .eq('email', email)
                .single();
            
            if (!userExists) {
                throw new Error('   ');
            }

            //  OTP   
            const { error: otpError } = await supabase.auth.signInWithOtp({
                email,
                options: {
                    shouldCreateUser: false,
                    emailRedirectTo: null,
                    type: 'signup'
                }
            });

            if (otpError) throw otpError;

            //     
            return { 
                data: { 
                    user: { email } 
                }, 
                error: null 
            };
        } catch (error) {
            console.error('Sign in error:', error);
            return { data: null, error };
        }
    },

    signOut: async () => {
        try {
            const { error } = await supabase.auth.signOut();
            if (error) throw error;
            return { error: null };
        } catch (error) {
            console.error('Sign out error:', error);
            return { error };
        }
    },

    onAuthStateChange: (callback) => {
        return supabase.auth.onAuthStateChange((event, session) => {
            callback(event, session);
        });
    },

    getSession: async () => {
        try {
            const { data: { session }, error } = await supabase.auth.getSession();
            if (error) throw error;
            return { session, error: null };
        } catch (error) {
            console.error('Get session error:', error);
            return { session: null, error };
        }
    },

    channel: (channelName) => {
        return supabase.channel(channelName);
    }
};

// API    
const usersApi = {
    createUser: async (userData) => {
        try {
            console.log('Creating user:', userData);
            const { data, error } = await supabase
                .from('users')
                .insert([userData])
                .select();
            
            if (error) {
                console.error('Create user error:', error);
                throw error;
            }
            
            console.log('User created successfully:', data);
            return { data, error: null };
        } catch (error) {
            console.error('Create user error:', error);
            return { data: null, error };
        }
    },

    getUserById: async (userId) => {
        try {
            console.log('Fetching user by ID:', userId);
            //  ,      
            const { data, error } = await supabase
                .from('users')
                .select('*')
                .eq('id', userId);
                
            if (error) {
                console.error('Error in getUserById:', error);
                throw error;
            }
            
            // ,   
            if (!data || data.length === 0) {
                console.log('No user found with ID:', userId);
                return { data: null, error: null };
            }
            
            //    
            console.log('User found:', data[0]);
            return { data: data[0], error: null };
        } catch (error) {
            console.error('Get user error:', error);
            return { data: null, error };
        }
    },

    updateUser: async (id, userData) => {
        try {
            const { data, error } = await supabase
                .from('users')
                .update(userData)
                .eq('id', id)
                .select();
            if (error) throw error;
            return { data, error: null };
        } catch (error) {
            console.error('Update user error:', error);
            return { data: null, error };
        }
    },

    updateSession: async (userId, sessionData) => {
        try {
            console.log('Updating session for user:', userId);
            const { data, error } = await supabase
                .from('users')
                .update({ session: sessionData })
                .eq('id', userId)
                .select();
                
            if (error) {
                console.error('Error updating session:', error);
                throw error;
            }
            
            console.log('Session updated successfully:', data);
            return { data, error: null };
        } catch (error) {
            console.error('Update session error:', error);
            return { data: null, error };
        }
    }
};

// API    
const booksApi = {
    getBooks: async ({ sortField = 'title', sortDirection = 'asc', searchQuery = '' }) => {
        let query = supabase
            .from('books')
            .select(`
                *,
                author:authors(*),
                genre:genres(*)
            `)
            .order(sortField, { ascending: sortDirection === 'asc' });

        if (searchQuery) {
            query = query.ilike('title', `%${searchQuery}%`);
        }

        const { data, error } = await query;
        if (error) throw error;
        return { data, error };
    },

    createBook: async (bookData) => {
        const { data, error } = await supabase
            .from('books')
            .insert([bookData])
            .select();
        if (error) throw error;
        return { data, error };
    },

    updateBook: async (id, bookData) => {
        const { data, error } = await supabase
            .from('books')
            .update(bookData)
            .eq('id', id)
            .select();
        if (error) throw error;
        return { data, error };
    },

    deleteBook: async (id) => {
        const { error } = await supabase
            .from('books')
            .delete()
            .eq('id', id);
        if (error) throw error;
        return { error };
    }
};

