// API    
const storage = {
    uploadImage: async (file, path) => {
        const { data, error } = await supabase.storage
            .from('images')
            .upload(path, file);
        if (error) throw error;
        return { data, error };
    },

    deleteImage: async (path) => {
        const { error } = await supabase.storage
            .from('images')
            .remove([path]);
        if (error) throw error;
        return { error };
    },

    getImageUrl: (path) => {
        return supabase.storage
            .from('images')
            .getPublicUrl(path);
    }
};

//     
export const signUpWithEmail = async (email, password, userData) => {
    try {
        console.log('Starting registration process for:', email);
        
        //     OTP 
        const { data: authData, error: authError } = await supabase.auth.signUp({
            email,
            password,
            options: {
                data: {
                    first_name: userData.first_name,
                    last_name: userData.last_name,
                    middle_name: userData.middle_name,
                    class: userData.class,
                    role: userData.role
                },
                emailRedirectTo: null,
                channel: 'email',
                type: 'otp'
            }
        });

        if (authError) {
            console.error('Auth signup error:', authError);
            throw authError;
        }

        console.log('OTP sent successfully:', authData);
        
        //        
        return { 
            data: {
                ...authData,
                userData: {
                    first_name: userData.first_name,
                    last_name: userData.last_name,
                    middle_name: userData.middle_name,
                    class: userData.class,
                    role: userData.role,
                    email: email,
                    password: password
                }
            }, 
            error: null 
        };
    } catch (error) {
        console.error('Error in signUpWithEmail:', error);
        return { data: null, error };
    }
};

//      
export const createUserAfterVerification = async (userId, userData) => {
    try {
        console.log('Creating user record after verification:', userId);

        //     users
        const { error: userError } = await supabase
            .from('users')
            .insert([
                {
                    id: userId,
                    first_name: userData.first_name,
                    last_name: userData.last_name,
                    middle_name: userData.middle_name,
                    class: userData.class,
                    role: userData.role,
                    email: userData.email
                }
            ]);

        if (userError) {
            console.error('User creation error:', userError);
            throw userError;
        }

        return { error: null };
    } catch (error) {
        console.error('Error in createUserAfterVerification:', error);
        return { error };
    }
};

