import React, { createContext, useState, useContext, useEffect } from 'react';

const CurrencyContext = createContext();

export const CurrencyProvider = ({ children }) => {
    const [currentCurrency, setCurrentCurrency] = useState('USD');
    const [exchangeRates, setExchangeRates] = useState({
        USD: 1,
        RUB: 90,  //   ,    
        BYN: 3.2  //   ,    
    });
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    const currencies = {
        USD: {
            flag: 'https://flagcdn.com/w20/us.png',
            symbol: '$'
        },
        RUB: {
            flag: 'https://flagcdn.com/w20/ru.png',
            symbol: ''
        },
        BYN: {
            flag: 'https://flagcdn.com/w20/by.png',
            symbol: 'Br'
        }
    };

    useEffect(() => {
        const fetchExchangeRates = async () => {
            try {
                setLoading(true);
                setError(null);
                const response = await fetch('https://api.exchangerate-api.com/v4/latest/USD');
                const data = await response.json();
                setExchangeRates({
                    USD: 1,
                    RUB: data.rates.RUB,
                    BYN: data.rates.BYN
                });
            } catch (error) {
                console.error('    :', error);
                setError(error.message);
            } finally {
                setLoading(false);
            }
        };

        fetchExchangeRates();
        const interval = setInterval(fetchExchangeRates, 3600000); //    

        return () => clearInterval(interval);
    }, []);

    const convertPrice = (price, currency = currentCurrency) => {
        if (!price) return 0;
        return price * (exchangeRates[currency] || 1);
    };

    const formatPrice = (price, currency = currentCurrency) => {
        if (!price) return ' ';
        const convertedPrice = convertPrice(price, currency);
        return new Intl.NumberFormat('en-US', {
            style: 'currency',
            currency: currency,
            maximumFractionDigits: 0
        }).format(convertedPrice);
    };

    const changeCurrency = (currency) => {
        setCurrentCurrency(currency);
    };

    return (
        <CurrencyContext.Provider value={{
            currentCurrency,
            setCurrentCurrency,
            exchangeRates,
            currencies,
            convertPrice,
            formatPrice,
            loading,
            error,
            changeCurrency
        }}>
            {children}
        </CurrencyContext.Provider>
    );
};

export const useCurrency = () => {
    const context = useContext(CurrencyContext);
    if (!context) {
        throw new Error('useCurrency    CurrencyProvider');
    }
    return context;
};

export default CurrencyContext; 