import React, { useState, useEffect } from 'react';
import { Card, Form, InputGroup, Row, Col, Button } from 'react-bootstrap';
import Slider from '@mui/material/Slider';
import { useCurrency } from '../context/CurrencyContext';
import { Search } from 'react-bootstrap-icons';
import './Filters.css';

const Filters = ({
    searchQuery,
    setSearchQuery,
    minPrice,
    setMinPrice,
    maxPrice,
    setMaxPrice,
    selectedAuthor,
    setSelectedAuthor,
    selectedGenre,
    setSelectedGenre,
    authors,
    genres,
    countMatchingBooks
}) => {
    const { currentCurrency, currencies } = useCurrency();
    const [priceRange, setPriceRange] = useState([minPrice, maxPrice]);

    const handlePriceChange = (event, newValue) => {
        setPriceRange(newValue);
    };

    const handlePriceChangeCommitted = (event, newValue) => {
        setMinPrice(newValue[0]);
        setMaxPrice(newValue[1]);
    };

    const handlePriceInputChange = (type, value) => {
        const numValue = value === '' ? 0 : Number(value);
        const newRange = [...priceRange];
        
        if (type === 'min') {
            newRange[0] = Math.min(numValue, priceRange[1]);
            setPriceRange(newRange);
            setMinPrice(newRange[0]);
        } else {
            newRange[1] = Math.max(priceRange[0], numValue);
            setPriceRange(newRange);
            setMaxPrice(newRange[1]);
        }
    };

    const handleReset = () => {
        setSearchQuery('');
        setSelectedAuthor('');
        setSelectedGenre('');
        setMinPrice(0);
        setMaxPrice(10000);
        setPriceRange([0, 10000]);
    };

    const formatPriceLabel = (value) => {
        return `${value.toLocaleString()} ${currencies[currentCurrency].symbol}`;
    };

    return (
        <div className="filters-container">
            <h5 className="filters-title"></h5>
            
            <div className="filter-section">
                <InputGroup>
                    <InputGroup.Text className="search-icon-container">
                        <Search className="search-icon" />
                    </InputGroup.Text>
                    <Form.Control
                        type="text"
                        placeholder="    ..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        className="search-input"
                    />
                </InputGroup>
            </div>

            <Card className="filters-card">
                <Card.Body>
                    <div className="filter-section">
                        <h5></h5>
                        <Row className="mb-3">
                            <Col>
                                <InputGroup>
                                    <InputGroup.Text></InputGroup.Text>
                                    <Form.Control
                                        type="number"
                                        value={priceRange[0] === 0 ? '' : Math.round(priceRange[0])}
                                        onChange={(e) => handlePriceInputChange('min', e.target.value)}
                                        min={0}
                                        max={priceRange[1]}
                                        placeholder="0"
                                    />
                                    <InputGroup.Text>{currencies[currentCurrency].symbol}</InputGroup.Text>
                                </InputGroup>
                            </Col>
                            <Col>
                                <InputGroup>
                                    <InputGroup.Text></InputGroup.Text>
                                    <Form.Control
                                        type="number"
                                        value={priceRange[1] === 0 ? '' : Math.round(priceRange[1])}
                                        onChange={(e) => handlePriceInputChange('max', e.target.value)}
                                        min={priceRange[0]}
                                        max={10000}
                                        placeholder="10000"
                                    />
                                    <InputGroup.Text>{currencies[currentCurrency].symbol}</InputGroup.Text>
                                </InputGroup>
                            </Col>
                        </Row>
                        <Slider
                            value={priceRange}
                            onChange={handlePriceChange}
                            onChangeCommitted={handlePriceChangeCommitted}
                            min={0}
                            max={10000}
                            step={100}
                            valueLabelDisplay="auto"
                            valueLabelFormat={formatPriceLabel}
                            className="price-slider"
                        />
                    </div>

                    <div className="filter-section">
                        <h5></h5>
                        <Form.Select
                            value={selectedAuthor || ''}
                            onChange={(e) => setSelectedAuthor(e.target.value)}
                        >
                            <option value=""> </option>
                            {authors.map(author => {
                                const count = countMatchingBooks('author', author.id);
                                return (
                                    <option key={author.id} value={author.id}>
                                        {author.name} ({count})
                                    </option>
                                );
                            })}
                        </Form.Select>
                    </div>

                    <div className="filter-section">
                        <h5></h5>
                        <Form.Select
                            value={selectedGenre || ''}
                            onChange={(e) => setSelectedGenre(e.target.value)}
                        >
                            <option value=""> </option>
                            {genres.map(genre => {
                                const count = countMatchingBooks('genre', genre.id);
                                return (
                                    <option key={genre.id} value={genre.id}>
                                        {genre.name} ({count})
                                    </option>
                                );
                            })}
                        </Form.Select>
                    </div>

                    <Button 
                        variant="outline-secondary" 
                        className="w-100 mt-3"
                        onClick={handleReset}
                    >
                         
                    </Button>
                </Card.Body>
            </Card>
        </div>
    );
};

export default Filters;