import React, { useState, useEffect } from 'react';
import { Modal, Form, Button, Alert } from 'react-bootstrap';
import { booksApi, authorsApi, genresApi } from '../../../lib/supabase';

const EditBook = ({ show, onHide, book, onSuccess }) => {
    const [formData, setFormData] = useState({
        title: '',
        author_id: '',
        genre_id: '',
        publisher_id: '',
        isbn: '',
        publication_year: '',
        description: '',
        cover_image: null
    });
    const [authors, setAuthors] = useState([]);
    const [genres, setGenres] = useState([]);
    const [publishers, setPublishers] = useState([]);
    const [error, setError] = useState(null);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        if (book) {
            setFormData({
                title: book.title || '',
                author_id: book.author_id || '',
                genre_id: book.genre_id || '',
                publisher_id: book.publisher_id || '',
                isbn: book.isbn || '',
                publication_year: book.publication_year || '',
                description: book.description || '',
                cover_image: book.cover_image || null
            });
        }
        loadData();
    }, [book]);

    const loadData = async () => {
        try {
            //  
            const { data: authorsData, error: authorsError } = await authorsApi.getAuthors();
            if (authorsError) throw authorsError;
            setAuthors(authorsData);

            //  
            const { data: genresData, error: genresError } = await genresApi.getGenres();
            if (genresError) throw genresError;
            setGenres(genresData);

            //  
            const { data: publishersData, error: publishersError } = await booksApi.getPublishers();
            if (publishersError) throw publishersError;
            setPublishers(publishersData);
        } catch (e) {
            console.error('   :', e);
            setError('   ');
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError(null);
        setLoading(true);

        try {
            const { error: updateError } = await booksApi.updateBook(book.id, {
                title: formData.title,
                author_id: formData.author_id,
                genre_id: formData.genre_id,
                publisher_id: formData.publisher_id,
                isbn: formData.isbn,
                publication_year: formData.publication_year,
                description: formData.description,
                cover_image: formData.cover_image
            });

            if (updateError) throw updateError;

            onSuccess();
            onHide();
        } catch (e) {
            console.error('   :', e);
            setError('   ');
        } finally {
            setLoading(false);
        }
    };

    return (
        <Modal show={show} onHide={onHide} centered>
            <Modal.Header closeButton>
                <Modal.Title> </Modal.Title>
            </Modal.Header>
            <Form onSubmit={handleSubmit}>
                <Modal.Body>
                    {error && <Alert variant="danger">{error}</Alert>}
                    <Form.Group className="mb-3">
                        <Form.Label> </Form.Label>
                        <Form.Control
                            type="text"
                            value={formData.title}
                            onChange={(e) => setFormData({ ...formData, title: e.target.value })}
                            required
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label></Form.Label>
                        <Form.Select
                            value={formData.author_id}
                            onChange={(e) => setFormData({ ...formData, author_id: e.target.value })}
                            required
                        >
                            <option value=""> </option>
                            {authors.map(author => (
                                <option key={author.id} value={author.id}>
                                    {author.name}
                                </option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label></Form.Label>
                        <Form.Select
                            value={formData.genre_id}
                            onChange={(e) => setFormData({ ...formData, genre_id: e.target.value })}
                            required
                        >
                            <option value=""> </option>
                            {genres.map(genre => (
                                <option key={genre.id} value={genre.id}>
                                    {genre.name}
                                </option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label></Form.Label>
                        <Form.Select
                            value={formData.publisher_id}
                            onChange={(e) => setFormData({ ...formData, publisher_id: e.target.value })}
                            required
                        >
                            <option value=""> </option>
                            {publishers.map(publisher => (
                                <option key={publisher.id} value={publisher.id}>
                                    {publisher.name}
                                </option>
                            ))}
                        </Form.Select>
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label>ISBN</Form.Label>
                        <Form.Control
                            type="text"
                            value={formData.isbn}
                            onChange={(e) => setFormData({ ...formData, isbn: e.target.value })}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label> </Form.Label>
                        <Form.Control
                            type="number"
                            value={formData.publication_year}
                            onChange={(e) => setFormData({ ...formData, publication_year: e.target.value })}
                        />
                    </Form.Group>
                    <Form.Group className="mb-3">
                        <Form.Label></Form.Label>
                        <Form.Control
                            as="textarea"
                            rows={3}
                            value={formData.description}
                            onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                        />
                    </Form.Group>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={onHide}>
                        
                    </Button>
                    <Button variant="primary" type="submit" disabled={loading}>
                        {loading ? '...' : ''}
                    </Button>
                </Modal.Footer>
            </Form>
        </Modal>
    );
};

export default EditBook; 