import React from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';
import { authRoutes, publicRoutes } from '../routes';
import { useAuth } from '../contexts/AuthContext';
import { observer } from 'mobx-react-lite';
import { SHOP_ROUTE, PROFILE_ROUTE } from '../utils/consts';

const AppRouter = observer(() => {
    const { user } = useAuth();

    //   
    const hasLibrarianAccess = Boolean(user?.role === 'LIBRARIAN');

    return (
        <Routes>
            {/* -    */}
            {hasLibrarianAccess && authRoutes.map(({path, Component}) =>
                path === '/admin' && <Route key={path} path={path} element={<Component/>} exact/>
            )}
            {/*      */}
            {user && authRoutes.map(({path, Component}) =>
                path === PROFILE_ROUTE && <Route key={path} path={path} element={<Component/>} exact/>
            )}
            {publicRoutes.map(({path, Component}) =>
                <Route key={path} path={path} element={<Component/>} exact/>
            )}
            <Route path="*" element={<Navigate to={SHOP_ROUTE} />} />
        </Routes>
    );
});

export default AppRouter; 