import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Nav, Button, Form, Alert, Spinner } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    faBook,
    faPenFancy,
    faTags,
    faUsers,
    faBars,
    faChevronLeft,
    faWarehouse,
    faExchangeAlt
} from '@fortawesome/free-solid-svg-icons';
import BooksTable from '../components/tables/BooksTable';
import AuthorsTable from '../components/tables/AuthorsTable';
import GenresTable from '../components/tables/GenresTable';
import RatingsTable from '../components/tables/RatingsTable';
import UsersTable from '../components/tables/UsersTable';
import StockTable from '../components/tables/StockTable';
import LoansTable from '../components/tables/LoansTable';
import './Admin.css';

const Admin = () => {
    const { user, loading } = useAuth();
    const navigate = useNavigate();
    const [error, setError] = useState('');
    const [activeTab, setActiveTab] = useState('books');
    const [isCollapsed, setIsCollapsed] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');

    useEffect(() => {
        console.log('Admin component mounted, current state:', {
            user: user ? {
                id: user.id,
                email: user.email,
                role: user.role
            } : null,
            loading,
            error
        });
        
        if (!loading) {
            if (!user) {
                console.log('No user found, redirecting to login');
                setError(',        ');
                setTimeout(() => navigate('/login'), 2000);
            } else if (user.role !== 'LIBRARIAN') {
                console.log('User is not a librarian:', user.role);
                setError(' .        .');
                setTimeout(() => navigate('/'), 2000);
            } else {
                console.log('Access granted for librarian:', user.role);
                setError('');
            }
        }
    }, [user, loading, navigate]);

    //      
    if (loading) {
        console.log('Admin component: Loading state');
        return (
            <div className="d-flex justify-content-center align-items-center" style={{ minHeight: '100vh' }}>
                <Spinner animation="border" role="status">
                    <span className="visually-hidden">...</span>
                </Spinner>
            </div>
        );
    }

    //  ,   
    if (error) {
        console.log('Admin component: Error state:', error);
        return (
            <div className="container mt-4">
                <Alert variant="danger">{error}</Alert>
            </div>
        );
    }

    //   
    if (!user || user.role !== 'LIBRARIAN') {
        console.log('Admin component: No access, user:', user);
        return null;
    }

    console.log('Admin component: Rendering admin panel for user:', {
        id: user.id,
        email: user.email,
        role: user.role
    });

    const getTabTitle = (tab) => {
        const titles = {
            books: ' ',
            authors: ' ',
            genres: ' ',
            ratings: ' ',
            users: ' ',
            stock: ' ',
            loans: ' '
        };
        return titles[tab] || tab;
    };

    const handleTabChange = (tab) => {
        setActiveTab(tab);
        setSearchQuery('');
    };

    const getButtonVariant = (tab) => {
        return activeTab === tab ? 'primary' : 'outline-primary';
    };

    return (
        <Container fluid className="p-0">
            <Row className="g-0">
                <Col md={2} className="sidebar">
                    <Nav className="flex-column">
                        <Nav.Link
                            className={activeTab === 'books' ? 'active' : ''}
                            onClick={() => handleTabChange('books')}
                        >
                            <FontAwesomeIcon icon={faBook} className="me-2" />
                            
                        </Nav.Link>
                        <Nav.Link
                            className={activeTab === 'authors' ? 'active' : ''}
                            onClick={() => handleTabChange('authors')}
                        >
                            <FontAwesomeIcon icon={faPenFancy} className="me-2" />
                            
                        </Nav.Link>
                        <Nav.Link
                            className={activeTab === 'genres' ? 'active' : ''}
                            onClick={() => handleTabChange('genres')}
                        >
                            <FontAwesomeIcon icon={faTags} className="me-2" />
                            
                        </Nav.Link>
                        <Nav.Link
                            className={activeTab === 'users' ? 'active' : ''}
                            onClick={() => handleTabChange('users')}
                        >
                            <FontAwesomeIcon icon={faUsers} className="me-2" />
                            
                        </Nav.Link>
                        <Nav.Link
                            className={activeTab === 'ratings' ? 'active' : ''}
                            onClick={() => handleTabChange('ratings')}
                        >
                            <FontAwesomeIcon icon={faBars} className="me-2" />
                            
                        </Nav.Link>
                        <Nav.Link
                            className={activeTab === 'stock' ? 'active' : ''}
                            onClick={() => handleTabChange('stock')}
                        >
                            <FontAwesomeIcon icon={faWarehouse} className="me-2" />
                            
                        </Nav.Link>
                        <Nav.Link
                            className={activeTab === 'loans' ? 'active' : ''}
                            onClick={() => handleTabChange('loans')}
                        >
                            <FontAwesomeIcon icon={faExchangeAlt} className="me-2" />
                            
                        </Nav.Link>
                    </Nav>
                </Col>
                <Col md={10} className="content">
                    <div className="p-4">
                        <div className="d-flex justify-content-between align-items-center mb-4">
                            <h2 className="mb-0">
                                {getTabTitle(activeTab)}
                            </h2>
                            <Form.Control
                                type="search"
                                placeholder="..."
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.target.value)}
                                style={{ maxWidth: '300px' }}
                            />
                        </div>

                        {error && <Alert variant="danger">{error}</Alert>}

                        {activeTab === 'books' && <BooksTable key="books" searchQuery={searchQuery} setSearchQuery={setSearchQuery} />}
                        {activeTab === 'authors' && <AuthorsTable key="authors" searchQuery={searchQuery} setSearchQuery={setSearchQuery} />}
                        {activeTab === 'genres' && <GenresTable key="genres" searchQuery={searchQuery} setSearchQuery={setSearchQuery} />}
                        {activeTab === 'users' && <UsersTable key="users" searchQuery={searchQuery} setSearchQuery={setSearchQuery} />}
                        {activeTab === 'ratings' && <RatingsTable key="ratings" searchQuery={searchQuery} setSearchQuery={setSearchQuery} />}
                        {activeTab === 'stock' && <StockTable key="stock" searchQuery={searchQuery} setSearchQuery={setSearchQuery} />}
                        {activeTab === 'loans' && <LoansTable key="loans" searchQuery={searchQuery} setSearchQuery={setSearchQuery} />}
                    </div>
                </Col>
            </Row>
        </Container>
    );
};

export default Admin; 